    monospace;
}

.site-header {
  background-color: rgba(0, 0, 0, 0.959);
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr;
}

.navSelections ul{
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.navSelections ul li {
  margin: 0 10px;
  color: white;
  list-style: none;
}

.navSelections, .linkSelection {
  grid-row: 1/2;
  grid-column: 1/2;
}

.linkSelection svg {
  margin: auto 5px;
}

.navSelections a, .navSelections:visited  {
  /* color: white; */
  font-size: 20px;
  text-decoration: none;
  padding-bottom: 3px;
  border-bottom: 2px #727272 solid;
}

.navSelections a:hover {
  color: white;
  border-bottom: 2px white solid;
}

#navIcon {
  color: #727272;
  transition: color .15s ease-in-out;
}
#navIcon:hover {
  transform: scale(1.1);
  transition-duration: .1s;
}

.btn-group a:hover{
  transform: scale(1.05);
  transition-duration: .1s;
}

.caroselContainer {
  width: 80%;
  margin: auto;
  padding: 50px;
}

#cardCoverImage {
  position: absolute;
}

.col:hover #cardCoverImage:hover {
  opacity: 0 !important;
}

.card-text + small {
  height: 100%;
  line-height: 30px;
}

.nav-pills .nav-item:hover {
  cursor: pointer;
  transition-duration: .2s;
  background-color: #356efd42;
  border-radius: 5px;
}

.col {
  color: inherit;
  text-decoration: none;
  transition-duration: .2s;
}
.col:hover {
  color: inherit;
  text-decoration: none;
  transition-duration: .2s;
  transform: scale(1.03);
}

.card a, .container svg {
  color: inherit;
  z-index: 60;
  font-size: 16px;
}

.card a:hover, .container svg:hover {
  color: #356efd
}

.footerText {
  color: #6c757d;
}
.parallaxBackground {
    width: 100%;
    height: 50vw;
    max-width: 100%;
    position: relative;
    overflow: hidden;
}

.sky {
    position: absolute;
    top: 0;
    width: 100%;
    left: 0;
    z-index: 20;
}

.ground {
    position: absolute;
    bottom: -10%;
    width: 100%;
    left: 0;
    z-index: 25;
}

.hillRight {
    position: absolute;
    bottom: -15%;
    width: 90%;
    right: 0;
    z-index: 24;
}

.mountainRight {
    position: absolute;
    bottom: 9%;
    width: 62%;
    right: 0;
    z-index: 21;
}

.mountainLeft {
    position: absolute;
    bottom: -35%;
    left: 0;
    width: 90%;
    z-index: 22;
}

.mainTitle {
    position: absolute;
    z-index: 20;
    top: 0;
    width: 100%;
    height: 100%;
    margin: auto;
    left: 0;
    right: 0;
    text-align: center;
    color:white;
    background:radial-gradient(circle, #15151585, #15151559 20%, transparent 50%);
}

.textContainer {
    position: relative;
    top: 30%;
    width: 100%;
    height: 100%;
}

.textContainer h1 {
    font-family: 'Poppins';
    font-weight: bold;
    margin-bottom: 0 !important;
}
.textContainer p {
    font-family: 'Poppins';
    font-weight: 500;
}


.arrowDown {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 20%;
    z-index: 31;
    color: white;
    margin: auto;
    width: 5vw;   
    -webkit-animation: pulse 2s infinite;   
            animation: pulse 2s infinite;
}

.arrowDown:hover {
    cursor: pointer;
}

@-webkit-keyframes pulse {
	0% {
		transform: scale(1);
	}

    15% {
        transform: scale(1.3);
    }
    30% {
        transform: scale(1);
    }

	45% {
		transform: scale(1.3);
    }
    
    60% {
        transform: scale(1);
    }

	100% {
		transform: scale(1);
	}
}

@keyframes pulse {
	0% {
		transform: scale(1);
	}

    15% {
        transform: scale(1.3);
    }
    30% {
        transform: scale(1);
    }

	45% {
		transform: scale(1.3);
    }
    
    60% {
        transform: scale(1);
    }

	100% {
		transform: scale(1);
	}
}

.shadow {
    pointer-events: none;
    position: absolute;
    bottom: 0;
    height: 50%;
    width: 100%;
    left: 0;
    z-index: 26;
    background: linear-gradient(to top, #151515, transparent)
}






@media (max-width: 500px) {
    .textContainer p{
        font-size: 10px;
    }
}


@media (max-width: 1000px) {
    .parallaxBackground {
        width: 100%;
        height: 100vh;
    }
    
    .sky {
        height: 200vh;
        width: auto;
        left: -170%;
    }
    
    .ground {
        bottom: -20%;
        height: 100vh;
        width: auto;
    }

    .mountainLeft {
        bottom: -30%;
        left: -25%;
        height: 100vh;
        width: auto;
    }
    
    .textContainer {
        top: 25% !important;
    }
    
    .textContainer h1 {
        font-size: 3rem;
    }

    .textContainer p {
        font-size: 1rem;
    }
    
    
    .arrowDown {
        position: absolute;
        bottom: 50%;
        width: 20vw;   
    }

    .arrowDownPosition {
        position: absolute;
        left: 0;
        right: 0;
        bottom: 30%;
    }
    
    .arrowDown:hover {
        cursor: pointer;
    }
}

@media (max-width: 1200px) {
    .textContainer {
        top: 20%;
    }
}
@media (min-width: 1400px) {
    .textContainer h1{
        
    font-size: 90px;
    }
}
@media (min-width: 1500px) {
    .textContainer h1{
        
    font-size: 100px;
    }
}

@media (min-width: 1690px) {
    .textContainer h1{
        
    font-size: 120px;
    }
}
